import cmdNavBar from "@/components/cmd-nav-bar/cmd-nav-bar.vue"
import cmdPageBody from "@/components/cmd-page-body/cmd-page-body.vue"
import cmdTransition from "@/components/cmd-transition/cmd-transition.vue"
import cmdInput from "@/components/cmd-input/cmd-input.vue"

export default {
  components: {
    cmdNavBar,
    cmdPageBody,
    cmdTransition,
    cmdInput
  },

  data() {
    return {
      account: {
        username: '',
        password: ''
      },
      usernameReg: /^[A-Za-z0-9]+$/,
      passwordReg: /^\w+$/,
      registerAccount: false,
      mobile: {
        phone: '',
        code: ''
      },
      phoneReg: /^[1](([3][0-9])|([4][5-9])|([5][0-3,5-9])|([6][5,6])|([7][0-8])|([8][0-9])|([9][1,8,9]))[0-9]{8}$/,
      registerMobile: false,
      safety: {
        time: 60,
        state: false,
        interval: ''
      },
      status: true // true手机注册,false账号注册
    };
  },

  watch: {
    /**
     * 监听手机注册数值
     */
    mobile: {
      handler(newValue) {
        if (this.phoneReg.test(newValue.phone) && newValue.code.length === 6) {
          this.registerMobile = true;
        } else {
          this.registerMobile = false;
        }
      },
      deep: true
    },
    /**
     * 监听账号注册数值
     */
    account: {
      handler(newValue) {
        if ((this.usernameReg.test(newValue.username) && newValue.username.length >= 8) && (this.passwordReg.test(
            newValue
            .password) && newValue.password.length >= 8)) {
          this.registerAccount = true;
        } else {
          this.registerAccount = false
        }
      },
      deep: true
    }
  },

  methods: {
    /**
     * 注册按钮点击执行
     */
    fnRegister() {
      if (this.status) {
        console.log(JSON.stringify(this.mobile));
      } else {
        console.log(JSON.stringify(this.account));
      }
    },
    /**
     * 获取验证码
     */
    fnGetPhoneCode() {
      if (this.phoneReg.test(this.mobile.phone)) {
        uni.showToast({
          title: "正在发送验证码",
          icon: "loading",
          success: () => {
            // 成功后显示倒计时60s后可在点击
            this.safety.state = true;
            // 倒计时
            this.safety.interval = setInterval(() => {
              if (this.safety.time-- <= 0) {
                this.safety.time = 60;
                this.safety.state = false;
                clearInterval(this.safety.interval);
              }
            }, 1000);
            uni.showToast({
              title: "发送成功",
              icon: "success"
            })
          }
        })
      } else {
        uni.showToast({
          title: "手机号不正确",
          icon: "none"
        })
      }
    },
    /**
     * 改变注册方式状态
     */
    fnChangeStatus() {
      this.mobile = {
        phone: '',
        code: ''
      }
      this.registerAccount = false
      this.account = {
        username: '',
        password: ''
      }
      this.registerMobile = false
      // 验证码时间状态还原
      clearInterval(this.safety.interval);
      this.safety.time = 60;
      this.safety.state = false;
      // 可以延迟3后切换
      this.status = !this.status;
    }
  },

  beforeDestroy() {
    /**
     * 关闭页面清除轮询器
     */
    clearInterval(this.safety.interval);
  }
}
